*{
  box-sizing: border-box;
 
}
.home{
  display:flex;
  flex-direction: column;
  align-items: center;
}

.create_form input{
  width: 300px;
  padding: 10px;
  border-bottom: 2px solid ;
  outline: none;
}
.create_form button{
  padding: 10px;
  background-color: black;
  color:white;
  cursor: pointer;
}
.task{
  display: flex;
  align-items: center;
  width: 345px;
  justify-content: space-between;
  color: black;
  height: 35px;
  padding:  2px 5px 2px 5px;
  background-color: rgb(34,193,195);
  margin-top: 12px;
}
.checkbox{
  display: flex;
  align-items: center;
}
.line_through{
  text-decoration: line-through;
 
}
.checkbox p{
  margin-left: 6px;
}
.checkbox icon{
  margin-left: 5px;
  font-style: 15px;
}
.task div span{
  margin: 0px 5px 0px 4px;
}
.task div .icon{
  cursor: pointer;
}